ne su alcance normativo y resuelve conflictos de constitucionalidad. En su dimensión política, sus decisiones inciden directamente en la orientación de las políticas públicas, la legislación y el ejercicio del poder. Esta dualidad convierte a la Corte en un actor clave del equilibrio institucional y en una pieza central del Estado Social de Derecho.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/4.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Además, la Corte no actúa como un ente pasivo. Desarrolla una jurisprudencia viva, que responde a las dinámicas sociales y los retos de una democracia pluralista. Sus fallos se fundamentan en interpretaciones sistemáticas, históricas y teleológicas, lo que le ha permitido convertirse en un espacio de diálogo constitucional entre el Estado, la sociedad civil y los grupos históricamente excluidos (Álvarez Díaz, 2008).")]),_c('div',{staticClass:"bg-color-2 p-5",attrs:{"data-aos":"fade-left"}},[_c('h5',[_vm._v("Este protagonismo jurisprudencial se manifiesta en su capacidad para:")]),_c('ul',{staticClass:"lista-ul--color mb-0"},[_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Delimitar el alcance de los derechos fundamentales.")])]),_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Desarrollar principios como la igualdad sustancial, la dignidad humana y la solidaridad.")])]),_c('li',{staticClass:"mb-0 d-flex"},[_c('i',{staticClass:"far fa-arrow-alt-circle-right"}),_c('p',{staticClass:"mb-0"},[_vm._v("Establecer estándares jurisprudenciales obligatorios para todas las autoridades del Estado.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/5.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Así, la Corte vela no solo por la legalidad, sino también por la legitimidad democrática de las normas y decisiones que afectan a la ciudadanía.")]),_c('div',{staticClass:"bg-color-1 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Desde su instalación en 1992, ha resuelto miles de casos que han generado profundas transformaciones jurídicas y sociales. Temas como la interrupción voluntaria del embarazo, el matrimonio igualitario, el derecho al agua, el desplazamiento forzado, la justicia transicional, el acceso a la salud y la protección de los pueblos indígenas han sido objeto de análisis y decisiones relevantes, evidenciando su vocación garantista y su compromiso con el desarrollo progresivo de los derechos humanos.")])]),_c('p',[_vm._v("En este contexto, el estudio de la Corte Constitucional resulta imprescindible para quien se forma en Derecho. Comprender su funcionamiento, estructura, competencias e impacto institucional permite visualizar cómo se materializa la supremacía constitucional y cómo se resuelven los conflictos entre normas infra constitucionales y principios superiores del orden jurídico.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_1","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("1.1")]),_vm._v(" Naturaleza y funciones de la Corte Constitucional")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":r