us decisiones inciden en políticas públicas, en la interpretación de derechos, en la configuración del poder estatal y en la redefinición del contrato social colombiano. En este sentido, no puede entenderse como un tribunal ordinario, sino como una instancia que articula derecho, política, sociedad y ética pública.")]),_c('div',{staticClass:"bg-color-1 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Desde su primera integración en 1992, la Corte ha construido una jurisprudencia robusta y comprometida con valores como la dignidad humana, la igualdad, el pluralismo, la participación, la justicia material y la protección de poblaciones marginadas. Su interpretación del derecho ha trascendido la literalidad normativa, incorporando principios del derecho internacional de los derechos humanos, soft law, jurisprudencia comparada y estándares fijados por organismos internacionales (Sandoval Rojas, 2015).")])]),_c('p',[_vm._v("Las funciones principales de la Corte Constitucional son: ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-6"},[_c('div',{staticClass:"px-4 px-md-5"},[_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/13.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-lg-1"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Además de su función jurídica, la Corte tiene un papel simbólico y pedagógico. Sus sentencias son leídas y discutidas por sectores sociales organizados, medios, movimientos ciudadanos y comunidades afectadas, contribuyendo a una cultura constitucional basada en el respeto por los derechos, el debate público y la exigencia de responsabilidad estatal.")]),_c('p',{staticClass:"mb-5"},[_vm._v("La Corte Constitucional no actúa de manera aislada. Ha establecido un diálogo jurisprudencial con otros órganos nacionales e internacionales como la Corte Suprema de Justicia, el Consejo de Estado, la Corte Interamericana de Derechos Humanos y tribunales constitucionales de otros países. Este intercambio fortalece la interpretación constitucional y permite el desarrollo progresivo de los derechos (Navarro-Hernández, 2022).")]),_c('div',{staticClass:"bg-color-4 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto mb-3 mb-lg-0"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/14.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("La Corte Constitucional se constituye como una institución con vocación garantista, defensora de la coherencia del orden jurídico con los principios democráticos, la justicia material y la dignidad humana. Sus funciones van más allá del ámbito técnico y la posicionan como uno de los actores más influyentes en la transformación del derecho y la vida pública colombiana.")])])])])])]),_c('p',{staticClass:"mb-5"},[_vm._v("Estudiar esta Corte permite comprender no solo cómo opera el control constitucional, sino también cómo se materializa la promesa de un Estado comprometido con los derechos, la inclusión y la equidad. En las próximas unidades, se analizará cómo sus decisiones han impactado en el diseño institucional, la protección de derechos y los límites al poder político.")]),_c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_2","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("1.2")]),_vm._v(" Composición y designación de los magistrados")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/15.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('div',{staticClass:"bg-color-1 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Comprender la forma en que se compone y designa la Corte Constitucional es esencial para analizar su funcionamiento y legitimidad como tribunal constitucional en Colombia. A diferencia de otros órganos judiciales, la Corte no solo interpreta la Constitución en abstracto: sus decisiones impactan directamente el orden jurídico, la garantía de los derechos fundamentales y los límites del poder público. Por ello, el proceso de elección de sus integrantes debe asegurar independencia, idoneidad, pluralismo ideológico, transparencia y un compromiso real con el Estado Social de Derecho consagrado en la Constitución de 1991.")])]),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("De acuerdo con el artículo 239 de la Constitución, la Corte Constitucional está compuesta por nueve magistrados, elegidos por el Senado de la República para un período individual de ocho años, sin posibilidad de reelección. El proceso de elección tiene una estructura tripartita: las ternas son presentadas por el Presidente de la República, la Corte Suprema de Justicia y el Consejo de Estado, y el Senado elige uno de los candidatos por cada terna mediante votación pública y nominal. El proceso de designación de magistrados: ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/16.png"),"alt":""}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-3 mb-5"},[_c('div',{staticClass:"px-4 px-md-5 py-4"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-auto mb-3 mb-lg-0"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema1/22.svg")}})]),_c('div',{staticClass:"col-lg text-white"},[_c('p',{staticClass:"mb-0"},[_vm._v("Este diseño institucional busca evitar la concentración del poder de nominación en un solo órgano y asegurar un equilibrio entre las ramas del poder público, conforme al principio de colaboración armónica del artículo 113 de la Constitución (Montoya, 2013). La participación del Ejecutivo, el Judicial y el Legislativo tiene el objetivo de proteger la independencia de la Corte frente a presiones políticas o alineamientos ideológicos.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/23.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("El período de ocho años permite a cada magistrado aportar de manera significativa a la jurisprudencia sin provocar estancamientos institucionales. La renovación progresiva impide la formación de bloques ideológicos fijos y promueve el debate interno y la evolución doctrinal, en sintonía con las transformaciones sociales y jurídicas.")]),_c('div',{staticClass:"bg-color-1 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("No obstante, el proceso de elección ha sido objeto de críticas recurrentes. La academia, la sociedad civil y los medios han señalado que, en la práctica, el Senado a veces decide con base en acuerdos políticos y clientelismo, y no por la idoneidad técnica. Esto afecta la legitimidad del tribunal y ha generado propuestas de reforma orientadas a introducir evaluaciones públicas, entrevistas abiertas, análisis de antecedentes éticos y participación ciudadana.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-3 order-lg-2 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/24.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-9 order-lg-1"},[_c('div',{staticClass:"bg-color-2 p-5 mb-5",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Los requisitos constitucionales para ser magistrado son exigentes: ciudadanía colombiana por nacimiento, ejercicio profesional de al menos quince años o experiencia equivalente en el sector público, y una trayectoria destacada en derecho público y constitucional. Se espera que los candidatos posean una visión plural, garantista y un compromiso ético con los principios democráticos.")])]),_c('p',[_vm._v("La diversidad en los perfiles de los magistrados ha enriquecido los debates internos. Figuras como Eduardo Cifuentes Muñoz, Carlos Gaviria Díaz y María Victoria Calle Correa representan distintas vertientes doctrinales y enfoques jurisprudenciales, lo cual ha consolidado una cultura institucional respetuosa de la disidencia argumentada, visible en salvamentos y aclaraciones de voto (Montoya, 2013).")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/25.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("El carácter colegiado de la Corte exige que sus decisiones se adopten en sala plena, por mayoría de votos. La deliberación colectiva fortalece el contenido de las sentencias y permite visibilizar distintas interpretaciones jurídicas. Los magistrados pueden presentar salvamentos o aclaraciones de voto, lo cual enriquece el debate público y fortalece la transparencia institucional.")]),_c('div',{staticClass:"bg-color-4 p-5 mb-3",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-0"},[_vm._v("La rotación escalonada ha generado una evolución jurisprudencial constante. En algunos períodos, la Corte ha sido más progresista, en otros más conservadora, reflejando los cambios culturales, sociales y jurídicos del país. Esta variabilidad no implica inestabilidad, sino una capacidad adaptativa frente a las transformaciones del constitucionalismo contemporáneo.")])]),_c('p',[_vm._v("En síntesis, la forma en que se compone y se designa la Corte Constitucional constituye una garantía estructural de su legitimidad. El equilibrio entre poderes, la prohibición de reelección, los requisitos exigentes, la deliberación colegiada y la diversidad de sus integrantes son factores clave para consolidar una justicia constitucional imparcial, sólida y democrática. Cualquier reforma futura al sistema de elección deberá preservar estos principios y fortalecer la transparencia, asegurando que el interés superior siga siendo la protección del pacto constitucional y de los derechos de todos los ciudadanos.")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"col-12 col-md-6 col-lg-3 offset-lg-1"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/componentes/material-complementario.svg"),"alt":""}})])])}]

